.content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.content-blocks-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* justify-content: space-between; */
  width: 100%;
}

.content-blocks-group + .content-blocks-group {
  margin-top: var(--grid-row-height);
}
