.grid {
  visibility: hidden;
  position: absolute;
  top: 0;
  z-index: 999;
  pointer-events: none;
}

.grid.show-grid {
  visibility: visible;
}

.grid-rows,
.grid-columns {
  position: fixed;
}

.grid,
.grid-rows,
.grid-columns {
  width: calc(
    var(--grid-column-width) * var(--grid-columns-number) + var(--grid-gutter) * (var(--grid-columns-number) - 1)
  );
  height: calc(var(--grid-row-height) * var(--grid-rows-number));
}

.grid-columns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.grid-column {
  position: relative;
  width: var(--grid-column-width);
  height: 100%;
  /* background: rgba(0, 0, 0, 0.1); */
}

.grid-column.gutter {
  width: var(--grid-gutter);
}

.grid-column:not(.gutter):before {
  left: 0;
}

.grid-column:not(.gutter):after {
  right: 0;
}

.grid-column:not(.gutter):before,
.grid-column:not(.gutter):after {
  content: '';
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
}

.grid-row {
  position: relative;
  height: var(--grid-row-height);
}

.grid-row::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
}

.grid-row:last-child:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
}

.grid-column:not(.gutter):before,
.grid-column:not(.gutter):after,
.grid-row::before,
.grid-row:last-child:after {
  background: rgba(0, 0, 0, 0.3);
}

.grid-row.grid-row-focus {
  background: rgba(255, 255, 0, 0.1);
}
