.content-container {
  font-family: 'Inter', sans-serif;
  position: relative;
  flex: 1;
  background: #eceff1;
  overflow-x: hidden;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content-container.example {
  overflow: hidden;
}

.content-container.auto-scroll-content {
  overflow-y: hidden;
}

.content-container.result {
  overflow-y: auto !important;
}

.content-container-support {
  position: relative;
  width: 100%;
  height: 100%;
  flex: 1;
  max-width: 100%;
}

.content-container-support .borders {
  position: fixed;
  width: 100%;
  height: 100%;
  border: 1px solid #42a5f5;
  border-width: 0 1px;
  transform: translateX(-1px);
  opacity: 0.3;
}

.reading-progress-container {
  position: absolute;
  top: 0;
  left: 0;
}

.reading-progress {
  position: fixed;
  width: 10px;
  height: calc(100% - 48px);
  display: flex;
  flex-direction: column;
  z-index: 2;
  backdrop-filter: blur(3px);
}

.reading-progress-item {
  flex: 1;
  opacity: 0.2;
}

.reading-progress-item.not-completed {
  background: #f44336;
}

.reading-progress-item.completed {
  background: #4caf50;
}
