.content-block-type--text-image-vertical {
  flex-direction: column;
  height: 100%;
}

.content-block-type--text-image-vertical .content-block-item.text-container {
  flex: 1;
}

.auto-scroll-content .content-block-type--text-image-vertical .content-block-item.text-container {
  flex: none;
}

.auto-scroll-content .content-block-type--text-image-vertical {
  height: auto;
}
