.content-block-container {
  position: relative;
  max-width: 100%;
  cursor: default;
}

.content-container.edit .content-block-container.highlighted-block,
.content-container.edit .content-block-container:hover {
  outline: 1px solid #42a5f5;
}

.content-block {
  position: relative;
  display: flex;
  max-width: 100%;
}

.content-block-item {
  padding: var(--grid-row-height) var(--grid-gutter);
  background-color: #fff;
  height: 0%;
}
