.content-block-item.text-container {
  position: sticky;
  z-index: 2;
}

.content-block-item-title {
  font-size: var(--font-size-title);
  line-height: var(--grid-row-height);
  margin-bottom: var(--grid-row-height);
}

.content-block-item.text-container .text p {
  font-size: 0;
}

.content-block-item.text-container .text p + p {
  margin-top: var(--grid-row-height);
}

.content-block-item.text-container .text p span {
  position: relative;
  font-size: var(--font-size);
  line-height: var(--grid-row-height);
}

.auto-scroll-content .content-block-item.text-container .text .word:hover {
  background: #fff9c4 !important;
}

.auto-scroll-content .content-block-item.text-container .text .word.is-read {
  background: #dcedc8;
}
