.content-block-actions-container {
  display: none;
  position: absolute;
  top: calc(var(--grid-row-height) / 2);
  right: calc(var(--grid-column-width) / 2);
  z-index: 3;
}

.content-block-actions-container > button {
  background: #42a5f5;
  color: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
}

.content-block-actions-container > button:hover {
  background: #2196f3;
}

.content-container.edit .content-block-container.highlighted-block .content-block-actions-container,
.content-container.edit .content-block-container:hover .content-block-actions-container {
  display: block;
}
